.active-class {
  -webkit-filter: opacity(100%) !important;
          filter: opacity(100%) !important;
  text-decoration: none;
}

.nav-item {
  -webkit-filter: opacity(50%);
          filter: opacity(50%);
}

.nav-item:not(:last-child) {
  margin-right: 3rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.barbell {
  /* background-color: red; */
  position: relative;
  width: 40px;
  height: 40px;
}

.barbell > * {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 40px;
  height: 2.5px;
  background-color: #fff;
}

.barbell__weights {
  position: relative;
}

.barbell__weights--2 {
  transform: scaleX(-1) translateY(1px);
}

.barbell__weights > * > * {
  position: absolute;
  width: 2.5px;
  background-color: #fff;
  top: 50%;
  transform: translateY(-50%);
}

.weights__big {
  height: 20px;
  left: 5px;
}

.weights__med {
  height: 13px;
  left: 2.5px;
}

.weights__small {
  height: 6px;
}

.plate {
  position: relative;
  width: 40px;
  height: 40px;
}

.plate > * {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.plate__outline {
  height: 90%;
  width: 90%;
  color: transparent;
  border: solid 10px white;
}

.plate__circle {
  height: 10%;
  width: 10%;
  background-color: white;
}
@font-face {
  font-family: 'CMU Typewriter Regular';
  src: url(/barbell-loader/static/media/CMUTypewriter-Regular.71b56961.eot);
  src: url(/barbell-loader/static/media/CMUTypewriter-Regular.71b56961.eot?#iefix)
      format('embedded-opentype'),
    url(/barbell-loader/static/media/CMUTypewriter-Regular.d42873db.woff2) format('woff2'),
    url(/barbell-loader/static/media/CMUTypewriter-Regular.c605ec98.woff) format('woff'),
    url(/barbell-loader/static/media/CMUTypewriter-Regular.c93e777d.ttf) format('truetype'),
    url(/barbell-loader/static/media/CMUTypewriter-Regular.e9f93c27.svg#CMUTypewriter-Regular)
      format('svg');
  font-weight: 500;
  font-style: normal;
}

a:hover {
  text-decoration: none !important;
}

.info-icon {
  height: 40px;
  width: 40px;
  font-family: 'CMU Typewriter Regular';
  font-size: 1.5rem;
  color: white;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

.info-icon > * {
  position: absolute;
  top: 57%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.unit-btn {
  border-radius: 15%;
  background-color: #d9534f;
  color: white;
  text-align: center;
  font-size: 2rem;
  border: none;
  padding: 0.25rem 0.5rem;
}

h1 {
  font-size: 2rem;
  text-align: center;
  font-family: 'Roboto';
}

.container {
  margin-top: 1rem;
}
.container > * > * {
  margin-top: auto;
  margin-bottom: auto;
}
.container > * > h1 {
  padding: 0.25rem 0.5rem;
}

.plate-group {
  outline: none;
}

.plate-group > * {
  display: inline-block;
  position: relative;
  border-radius: 50%;
  color: #fff;
  margin: 2.5px 0;
}

.plate-group__plate {
  background-color: gray;
  padding: 2rem;
  font-size: 1.25rem;
  border: none;
}

.plate-group__quantity {
  background-color: #000;
  font-size: 1rem;
  padding: 0.85rem;
  position: absolute;
  bottom: 0%;
  margin-left: -5px;
  opacity: 0;
}

.load {
  position: absolute;
  bottom: 235px;
  margin-left: 10px;
  z-index: -10;
}

.load__barbell--shaft {
  position: absolute;
  top: 65%;
  left: -10000px;
  height: 15px;
  transform: translateY(-35%);
  width: 10280px;
  background-color: gray;
  z-index: -20;
}

.load__barbell--shaft > * {
  color: white;
  position: absolute;
  top: -35%;
  left: 9980px;
}

.load__barbell--sleeve-threshold {
  position: absolute;
  top: 60%;
  width: 15px;
  height: 30px;
  transform: translateY(-40%);
  background-color: gray;
  z-index: -19;
}

.plate-container {
  position: absolute;
}

.plate-container__plate {
  width: 30px;
}

.plate-container__plate--text {
  font-size: 1rem;
  color: white;
}

Form {
  margin: 0 auto;
  margin-top: 100px;
  width: 50%;
}

input:hover,
input:active {
  outline: 0px !important;
  -webkit-appearance: none;
  box-shadow: none !important;
}

.weight-input__form {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid black;
  text-align: right;
}

.weight-input__form::-webkit-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form:-ms-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form::-ms-input-placeholder {
  color: #a9a9a9;
}

.weight-input__form::placeholder {
  color: #a9a9a9;
}

.weight-input__text {
  background-color: transparent;
  color: #000;
  border: none;
}


.center-vertically {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bg-black {
  background-color: #000;
}
.bg-yellow {
  background-color: #f0ad4e;
}
.bg-red {
  background-color: #d9534f;
}
.bg-purple {
  background-color: #4b0082;
}
.bg-green {
  background-color: #5cb85c;
}
.bg-cyan {
  background-color: #5bc0de;
}
